body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.valign-top {
  vertical-align: top;
}

.livechat .card {
  height: 90vh; 
}

.livechat .card-body {
  overflow: scroll;
    height: 80vh;
}
.msg-container{
  margin-top: auto;
  margin-bottom: auto;
  max-width: 75%;
  margin-left: 10px;
  border-radius: 25px 25px 25px 0;
  background-color: #82ccdd;
  padding: 10px;
  position: relative;
}

.msg-container-send {
  margin-top: auto;
  margin-bottom: auto;
  max-width: 75%;
  margin-right: 10px;
  border-radius: 25px 25px 0 25px;
  background-color: #78e08f;
  padding: 10px;
  position: relative;
}

.tri-right.btm-left:after{
	content: ' ';
	position: absolute;
	width: 0;
	height: 0;
	left: 0px;
  right: auto;
  top: auto;
	bottom: -20px;
	border: 22px solid;
	border-color: transparent transparent transparent #82ccdd;
}
.tri-right.btm-right:after{
	content: ' ';
	position: absolute;
	width: 0;
	height: 0;
  left: auto;
	right: 0px;
	bottom: -20px;
	border: 12px solid;
	border-color: #78e08f #78e08f transparent transparent;
}
.cards {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:10px;
}

.cards :hover {
    background-color: red ;
    border: 1px solid #d0bfff;
}
.cards li {
    padding: 10px;
    border-radius: 5px;
}

.faq-container {
    height: 100%;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
}

.left-panel {
    display: -webkit-flex;
    display: flex;
    color: #777;
    -webkit-align-self: center;
            align-self: center;
    text-align: center;
}

.right-panel {
    border-left: thin solid #bbb;
    margin: 0 20px;
}

.right-panel > div {
    width: calc(100% - 5px);
    padding-left: 10px;
    padding-right: 10px;
}

.right-panel .btn-light {
    background-color: white;
}

.right-panel input {
    border-radius: 1rem;
}

.right-panel .row {
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    height: 150px;
}

.right-panel .box {
    display: -webkit-flex;
    display: flex;
    height: 100px;
    background: #fff;
    border-radius: 4px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background: transparent;
 }

 .right-panel .box:hover {
    background: white;
    border: thin solid #bbb;
    cursor: pointer;
 }

 .right-panel .col-sm-3 {
     margin-top: 10px;
 }

.email-form {
  padding-top: 20px;
} 
.react-tabs > ul {
  text-align: end;
}

.bg-whitesmoke {
  background-color: whitesmoke;
}
